.app_container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    padding: 0px;
    margin: 0px;
}

.camera_toolbar {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border-top: 1px solid #111;
    padding: 0px;
    float: bottom;
    .upload_image_button {
        border: 1px solid #111;
        border-radius: 0px;
        padding: 10px;
        margin: 10px;
        background: #111;
        color: #fff;
        font-size: 40pt;
    }
}

* {
    font-family: Avenir, Montserrat, Corbel, 'URW Gothic', source-sans-pro, sans-serif;
    font-weight: 400;
}